'use strict';

/*
* When adding modules to the namespace, ensure that they are added in alphabetical order according to module name.
*/

// MODULES //

import setReadOnly from '@stdlib/utils-define-read-only-property';


// MAIN //

/**
* Top-level namespace.
*
* @namespace ns
*/
var ns = {};

/**
* @name dmax
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/stats/base/ndarray/dmax}
*/
import dmax from '@stdlib/stats-base-ndarray-dmax';
setReadOnly( ns, 'dmax', dmax );

/**
* @name max
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/stats/base/ndarray/max}
*/
import max from '@stdlib/stats-base-ndarray-max';
setReadOnly( ns, 'max', max );

/**
* @name smax
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/stats/base/ndarray/smax}
*/
import smax from '@stdlib/stats-base-ndarray-smax';
setReadOnly( ns, 'smax', smax );


// EXPORTS //

export default ns;
